<template>
  <div>
    <!--引用返胎三包登记信息-->
    <el-dialog :close-on-click-modal="false" title="引用返胎登记信息" :visible="true" class="dialogForm" width="1300px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <div>
            <el-form-item label="客户名称" size="mini">
              <span>{{message.customer_name}}</span>
            </el-form-item>
            <el-form-item label="审核状态" size="mini">
              <span>已审核</span>
            </el-form-item>
          </div>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>

          <el-form-item label="返胎登记单据号" size="mini">
            <el-input v-model="params.register_sn" placeholder="请输入" class="shortSearchInput"></el-input>
          </el-form-item>

          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" placeholder="请输入" maxlength="300" class="shortSearchInput"></el-input>
          </el-form-item>
          <el-form-item label="返胎类型" size="mini">
            <el-select clearable v-model="params.return_type" placeholder="请选择">
              <el-option label="返新胎入库" value="1">返新胎入库</el-option>
              <el-option label="返旧胎入库" value="2">返旧胎入库</el-option>
            </el-select>
          </el-form-item>

          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <!-- <p class="dialogTips">单据类型：销售出库单</p><br/> -->
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" max-height="450">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="register_sn" width="140" label="返胎登记单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="return_type" label="返胎类型">
            <template slot-scope="scope">
              <span v-if="scope.row.return_type==1">返新胎入库</span>
              <span v-else>返旧胎入库</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_num" label="返胎数量">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期">
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()">取 消</el-button>
        <el-button class="tx" type="primary" @click="dialogSubmitForm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      authority: {},
      radio: "",
      TableData: {
        tableData: []
      },
      params: {},
      per_page: [30, 50, 100, 500],
      loading: true,
      searchParams: {},
      goPage: 1, //默认搜索页面
      pageSize: 30,
      timeValue: [],
      dialogmultipleSelection: [] //客户列表
    };
  },
  props: ["message"],
  methods: {
    handleSelectionChange(val) {
      this.addmultipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      let obj = Object.assign({}, this.params, {
        customer_id: this.message.customer_id,
        associated_customer_id: this.message.associated_customer_id
      });
      this.$axMethod.quoteRegisterList(obj).then(response => {
        this.TableData.tableData = response.data.data;
        this.TableData.tableData.forEach(item => {
          item.remark = "";
        });
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$axMethod
        .quoteRegisterList({
          customer_id: this.message.customer_id,
          associated_customer_id: this.message.associated_customer_id
        })
        .then(response => {
          this.TableData.tableData = response.data.data;
          this.TableData.tableData.forEach(item => {
            item.remark = "";
          });
        });
    },
    dialoghandleSelectionChange(val) {
      this.dialogmultipleSelection = val;
    },
    closeDialog() {
      //引用销售出库单据 弹出框取消
      this.$parent.salesDocumentsVisible = false;
    },
    dialogSubmitForm() {
      //返回引用的出库单单据数据
      if (this.addmultipleSelection) {
        this.$emit("getData", this.addmultipleSelection);
        this.$parent.salesDocumentsVisible = false;
      } else {
        this.$message({
          message: "请选择引用单据",
          type: "error"
        });
      }
    }
  },
  mounted() {
    this.$set(this, "searchParams", {
      params: {
        customer_id: this.message.customer_id,
        associated_customer_id: this.message.associated_customer_id
      },
      TableData: this.TableData, //列表数据
      API: "quoteRegisterList", //接口名
      vm: this //传递的this
    });
    this.$axMethod
      .quoteRegisterList({
        customer_id: this.message.customer_id,
        associated_customer_id: this.message.associated_customer_id
      })
      .then(response => {
        this.TableData.tableData = response.data.data;
        this.TableData.tableData.forEach(item => {
          item.remark = "";
        });
      });
  }
};
</script>

