<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>售后管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">返胎入库列表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>新增返胎入库</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="addFormInline" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini" prop="company_id" :rules="rules.company_id">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" :rules="rules.customer_name" size="mini" prop="customer_id">
          <el-select v-model="params.customer_id" clearable filterable placeholder="请输入" @change="getCustomName" value-key="customer_id" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="params.business_time" type="date" placeholder="选择日期" range-separator="-" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>

      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" :disabled="quoteOrderShow" @click="quoteOrder" class="addBtn" size="mini">引用单据</el-button>
      <el-button icon="el-icon-circle-plus-outline" type="primary" :disabled="addOrderShow" @click="addOrder" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <!-- 这里循环的 -->
      <el-table :highlight-current-row="true" tooltip-effect="light" ref="table" border :data="getMessage" style="width: 100%" max-height="600" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号">
        </el-table-column>
        <el-table-column property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="light" content="请选择客户名称" placement="top" v-if="!params.customer_id">
              <el-input type="text" placeholder="请选择" disabled></el-input>
            </el-tooltip>
            <v-GoodsList v-else :scopes='scope' :vxeParams='{company_id: params.company_id}' :changeGoods='handleSelect' :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="入库数量">
          <template slot-scope="scope">
            <el-input v-if="scope.row.edit" v-model="scope.row.goods_num"></el-input>
            <span v-else>{{scope.row.goods_num}}</span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="return_type" label="返胎类型">
          <template slot-scope="scope">
            <el-select v-if="scope.row.edit" clearable v-model="scope.row.return_type">
              <el-option label="返新胎入库" :value="1">返新胎入库</el-option>
              <el-option label="返旧胎入库" :value="0">返旧胎入库</el-option>
            </el-select>
            <template v-else>
              <span v-if="scope.row.return_type==1">返新胎入库</span>
              <span v-else>返旧胎入库</span>
            </template>

          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_id" label="仓库名称">
          <template slot-scope="scope">
            <el-select clearable v-model="scope.row.warehouse_id" placeholder="请选择" @focus="getWarehouseFouse" @change="warehouseChangeId(scope.row)">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList.options"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称">
          <template slot-scope="scope">
            <el-select :disabled="getwarehouseOption" v-model="scope.row.allocation_id" value-key="allocation_id" @change="val => tableAllocationChange(val, scope.row)" @focus="event => portTableAllocationList('', scope.row)" filterable remote placeholder="请输入" :loading="tableDownLoading">
              <el-option v-for="item in scope.row.allocationList " :key="item.allocation_id" :label="item.allocation_name " :value="item.allocation_id">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column property="remark" label="备注">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" placeholder="请输入" maxlength="300" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="submit('addFormInline')">确 定</el-button>
    </div>
    <threeinDalog :message="getCustomNames" :getCustomNameId="getCustomNameId" @getData="dongList" v-if="salesDocumentsVisible" />
  </div>
</template>

<script>
import threeinDalog from "@/components/element/aftersale/share/threein_dalog";
import { mapActions, mapGetters } from "vuex";
import { opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleThreeinAdd",
  data() {
    return {
      tableDownLoading: false, // 列表下拉加载字段
      quoteOrderShow: false,
      addOrderShow: false,
      authority: {},
      getCustomNameId: "",
      getCustomNames: {}, //传递给弹框组件
      params: {},
      multipleSelection: [],
      salesDocumentsVisible: false, //来源单号表弹框
      return_allocation_arr: {}, //当前选择的货位列表
      rowIndex: "",
      getMessage: [],
      warehouseList: {
        options: []
      }, //仓库管理
      options4: [],
      goodsList: [],
      list: [],
      loading: false,
      getwarehouseOption: true
    };
  },
  components: {
    threeinDalog //销售单据
  },
  computed: {
    ...mapGetters(["threeinAdd_Get", "threeinIndex_Get"])
  },
  methods: {
    ...mapActions(["threeinIndex"]),
    handleSelect(value, { row }) {
      this.$set(row, "brand_name", value.brand_name);
      this.$set(row, "brand_id", value.brand_id);
      this.$set(row, "goods_id", value.goods_id);
      let canAddGoods = this.getMessage[this.getMessage.length - 1].goods_id
        ? true
        : false;
      if (canAddGoods)
        this.getMessage.push({
          edit: true,
          return_type: 0
        });
      this.$nextTick(() => {
        this.$refs.table.bodyWrapper.scrollTop =
          this.$refs.table.bodyWrapper.scrollHeight + 33;
      });
    },
    change(val, val2) {
      this.getMessage[val2].brand_name = val.goods_name.brand_name;
      this.getMessage[val2].brand_id = val.goods_name.brand_id;
      this.getMessage[val2].goods_id = val.goods_name.goods_id;
    },
    getWarehouseFouse() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.params.company_id
      }); //仓库下拉列表
    },
    getCustomName(val) {
      this.getCustomNames = val;
    },
    // 表体 货位下拉 change 事件
    tableAllocationChange(val, row) {
      row.allocationList.some(item => {
        if (item.allocation_id == val) {
          row.allocation_name = item.allocation_name;
          return true;
        }
      });
    },
    warehouseChangeId(row) {
      // 仓库ID的获取
      this.getwarehouseOption = false;
      this.$set(row, "allocation_id", "");
      this.$set(row, "allocation_name", "");
      // this.$axMethod
      //   .allocationInventoryList({
      //     warehouse_id: val.warehouse_id
      //   })
      //   .then(response => {
      //     this.allocationList = response.data.data;
      //     this.list = this.allocationList.map(item => {
      //       return {
      //         allocation_id: item.allocation_id,
      //         allocation_name: item.allocation_name
      //       };
      //     });
      //     // 添加虚拟货位
      //     if (val.is_default) {
      //       this.options4 = this.list.concat([]);
      //       val.allocation_nameS = this.list[0];
      //     }
      //   });
      var box = this.warehouseList.options.filter(val => val.warehouse_id === row.warehouse_id)
      row.warehouse_name = box[0] ? box[0].warehouse_name : ''
    },
    // 获取货位下拉
    portTableAllocationList(query, row) {
      this.tableDownLoading = true; // 开启加载
      this.$set(row, "allocationList", []);
      this.$axMethod
        .allocationInventoryList({
          warehouse_id: row.warehouse_id,
          allocation_name: query
        })
        .then(({ data }) => {
          this.tableDownLoading = false; // 关闭加载
          if (data.code == 200) {
            row.allocationList = data.data;
          }
        })
        .catch(error => {
          this.tableDownLoading = false; // 关闭加载
        });
    },
    dongList(val) {
      var temp = [
        "brand_id",
        "brand_name",
        "business_time",
        "goods_id",
        "goods_name",
        "goods_num",
        "goods_sn",
        "register_goods_id",
        "register_id",
        "register_sn",
        "remark",
        "return_type",
        "return_type_name"
      ];
      var result = this.getMessage.concat([]);
      // 子组件参数传递过来
      if (val.length > 0) {
        this.addOrderShow = true;
        let batchIdArr = result.map(item => {
          return item.goods_id;
        });
        if (!batchIdArr[batchIdArr.length - 1]) {
          result.pop();
        }
        val.forEach(item => {
          if (!batchIdArr.includes(item.goods_id)) {
            var obj = {};
            temp.forEach(val => {
              obj[val] = item[val];
            });
            result.push(obj);
          }
        });

        this.params.type = 1;
        this.getMessage = result;
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    deletes() {
      if (this.multipleSelection.length) {
        let a = new Set(this.getMessage);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.getMessage = Array.from(differenceABSet);
        if (this.getMessage.length == 0) {
          this.quoteOrderShow = false;
          this.addOrderShow = false;
        }
      } else {
        this.$message({
          message: "请先选择要删除的数据！",
          type: "warning"
        });
      }
    },
    companyChange(val) {
      this.$set(this.params, "department_id", null); // 初始化部门名称下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
      this.$set(this, "getMessage", []);
    },
    goBack() {
      this.$router.push({
        path: "/aftersale/threein"
      });
      // wz cache
      this.threeinIndex({
        // moduleName: "companyIndex",
        goPage: this.threeinAdd_Get.goPage,
        pageSize: this.threeinAdd_Get.pageSize,
        // params: this.threeinAdd_Get.params
        params: this.threeinIndex_Get ? this.threeinIndex_Get.params : {}
      });
    },
    addOrder() {
      if (!!this.params.customer_id) {
        this.quoteOrderShow = true;
        this.params.type = 2;
        this.getMessage.push({
          edit: true,
          return_type: 0
        });
      } else {
        this.$message({
          message: "请先选择客户名称",
          type: "error"
        });
      }
    },
    quoteOrder() {
      if (!this.params.customer_id) {
        this.$message({
          message: "请先选择客户名称",
          type: "error"
        });
      } else {
        this.salesDocumentsVisible = true;
      }
      //来源单号
    },
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let vm = this;
          this.carDoctorWarehouse = [];
          let tableLen = this.getMessage.length;
          let hasLastGoodsName = !!this.getMessage[tableLen - 1].goods_name
            ? true
            : false; //最后一行数据是否包含商品名称
          if (!hasLastGoodsName) {
            this.getMessage.pop();
          }
          for (var x = 0; x < this.getMessage.length; x++) {
            if (!this.getMessage[x].allocation_id) {
              this.$message({
                message: "请先完善信息！",
                type: "warning"
              });
              return false;
            }
          }
          this.params.goods_json = [];
          let tire_json = this.getMessage.map(item => {
            this.carDoctorWarehouse.push({
              warehouse_name: item.warehouse_name,
              warehouse_id: item.warehouse_id
            });
            return {
              register_goods_id: item.register_goods_id
                ? item.register_goods_id
                : "",
              goods_id: item.goods_id,
              goods_num: item.goods_num,
              return_type: item.return_type,
              warehouse_id: item.warehouse_id,
              remark: item.remark,
              brand_id: item.brand_id,
              allocation_id: item.allocation_id,
              allocation_name: item.allocation_name
            };
          });

          this.params.goods_json = tire_json;
          let mustNameArr = "warehouse_id,allocation_id".split(",");

          var flag = true;
          this.params.goods_json.forEach(item => {
            for (const name of mustNameArr) {
              if (name in item) {
                if (!item[name]) {
                  flag = false;
                }
              } else {
                flag = false;
              }
              if (!flag) break;
            }
            if (!flag) {
              this.$message({
                message: "请先完善信息！",
                type: "warning"
              });
              return false;
            }
          });
          if (flag) {
            // 校验对接车郎中
            this.$baseData
              .checkCarDoctor({
                company_id: this.params.company_id,
                company_name: this.params.company_name,
                bill_type: 8,
                // bill_id: this.params.return_id,
                operation_type: 1,
                goods_info: JSON.stringify(this.carDoctorWarehouse)
              })
              .then(() => {
                vm.$axMethod
                  .addInStorage(
                    Object.assign({}, vm.params, {
                      goods_json: JSON.stringify(vm.params.goods_json),
                      customer_id: vm.getCustomNames.customer_id
                    })
                  )
                  .then(response => {
                    if (response.data.code == 200) {
                      vm.$message({
                        message: response.data.msg
                          ? response.data.msg
                          : response.data.data,
                        type: "success"
                      });
                      vm.goBack();
                    }
                  });
              });
          }
        }
      });
    }
  },
  mounted() {
    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id)
      });
    } else {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        department_id: Number(localStorage.department_id)
      });
    }
    opthionList(this, localStorage);
    this.getMessage.push({
      remark: "",
      edit: true,
      return_type: 0
    });
  }
};
</script>

<style lang="less" scoped>
.dialog-footer {
  margin-top: 40px;
  text-align: center;
}
.styleInput > .el-input {
  width: auto !important;
}

