  line-height: 50px;
  border-bottom: 1px solid #dddddd;
}

.el-form > div:nth-child(2n + 1) {
  clear: both;
}

.batch-ul {
  overflow: hidden;
}

.batch-ul2 {
  padding: 0 20px;
}

.line1 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.batch-ul li {
  float: left;
  line-height: 34px;
  height: 34px;
}

.batch-ul li:nth-child(1) {
  width: 140px;
}

.batch-ul li:nth-child(2) {
  width: 140px;
  text-align: right;
}
.el-table td div {
  width: 100%;
}
